.footer {
  margin: 0 0 60px 0;
}

.footer__text {
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #545454;
}
@media screen and (max-width: 320px) {
  .footer {
    margin: 0 0 36px 0;
  }
  .footer__text {
    font-size: 14px;
    line-height: 17px;
  }
}
